/* 作用：主体以及按需导入的第三方组件样式重写 */
/* 注意: 可导入到各组件中使用 */
.ant-form-item {
  margin-bottom: 8px;
}
.ant-table .operate-column {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.ant-table .operate-column__btn {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 4px;
}
.ant-modal {
  top: 20px;
  padding-bottom: 0;
}
.ant-modal-header {
  padding: 12px 24px;
}
.ant-modal-body {
  padding: 12px 24px;
  max-height: calc(100vh - 40px - 47px);
  overflow: hidden auto;
}
.ant-modal-close-x {
  width: 46px;
  height: 46px;
  line-height: 46px;
}
.ant-modal.full-height .ant-modal-body {
  height: calc(100vh - 40px - 47px);
}
.login-page {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  background: url('../../../assets/login/bg.jpg') center no-repeat;
  background-color: #3f8eed;
  background-size: cover;
  overflow: hidden;
}
.login-page__login {
  width: 403px;
  height: 450px;
  margin-right: 200px;
  padding: 0px 58px 0 62px;
  background: url('../../../assets/login/login_box.png') center no-repeat;
  background-size: contain;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.login-page__login-cn {
  font-size: 20px;
  line-height: 1;
  margin: 32px 0;
  color: #1458BD;
}
.login-page__login-box {
  width: 100%;
  overflow: hidden;
}
.login-page__login-form .ant-form-item {
  margin-bottom: 16px;
}
.login-page__login-tree .ant-select-selector {
  height: 38px !important;
}
.login-page__login-tree .ant-select-selection-item {
  line-height: 36px !important;
}
.login-page__login-input {
  line-height: 2;
}
.login-page__login-input .ant-input {
  padding-left: 6px !important;
}
.login-page__login-pwd {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  margin-bottom: 18px;
}
.login-page__login-verifyCode {
  height: 28px;
  cursor: pointer;
}
.login-page__login-btn {
  width: 100%;
  height: 42px;
  font-size: 16px;
  margin-top: 18px;
}
.login-page__login-error {
  width: 100%;
  color: #f04134;
  font-size: 12px;
  padding-top: 6px;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.login-page__copyright {
  position: absolute;
  left: 0;
  bottom: 12px;
  width: 100%;
  text-align: center;
}
.login-page__copyright p {
  margin: 0;
  color: #ffffff;
  font-size: 12px;
}
